@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Question__label{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.Question__label input{padding:16px}

.Elaborating__spinner{display:inline-block;width:80px;height:80px;margin:40px 0}.Elaborating__spinner::after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:4px solid #131429;border-color:#131429 transparent #131429 transparent;-webkit-animation:spinner 3s linear infinite;animation:spinner 3s linear infinite}@-webkit-keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spinner{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.Modal{background:rgba(0,0,0,0.65);-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);height:100%;position:fixed;transition:all .4s .2s ease-in-out;width:100%;z-index:1000}.Results{height:auto}.Results__songs{display:-webkit-flex;display:flex;list-style:none;-webkit-flex-wrap:wrap;flex-wrap:wrap}.Results__songs .Results__song{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;width:50%}.Song{-webkit-align-items:center;align-items:center;border:1px solid black;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;z-index:9999;position:absolute;background:white;top:25vh}.Song__buttons{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.Song__buttons .Song__play,.Song__buttons .Song__stop{width:50%}

*{box-sizing:border-box;margin:0;padding:0}body,html{height:100%;max-width:100%}html{font-size:100%}body::-webkit-scrollbar{display:none}body{background-color:#ececec;color:#131429;font-family:'Poppins', sans-serif}body{-ms-overflow-style:none;scrollbar-width:none}body.blocked{overflow-y:hidden}section{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:100vh;-webkit-justify-content:center;justify-content:center;padding:20vw}button{background-color:#131429;color:#ececec;border:0;border-radius:3px;font-family:'Poppins', sans-serif;padding:10px 20px}h1{font-size:28px}h2{font-size:22px}h3{font-size:20px}div,p,input{font-size:16px}

